#sidebar {
  position: relative;
  width: 56px;
  text-align: center;
  background-color: #2c333c;
}
#sidebar .icons {
  margin-top: 15px;
}
#sidebar .icons a {
  padding: 6px 0;
  display: block;
}
#sidebar .icons .router-link-active {
  background-color: #5e6266;
}
#sidebar .logout {
  position: absolute;
  bottom: 20px;
  width: 100%;
  text-align: center;
  cursor: pointer;
}
#sidebar .iconfont {
  color: #fff;
}
